const extract_csv_data = (data) => {
  if (!/^\w$/.test(data[0]))
    data = data.slice(1); // fix 1st bad char in csv
  const lines = data.split("\r\n")
  const headers = lines.shift().split(",")
  const csv_body = lines.map(l => l.split(","))
  const csv_mapped = csv_body.map(line => {
      const line_mapped = {}
      for (let i = 0; i < headers.length; i++) {
          const header = headers[i];
          line_mapped[header] = line[i];
      }
      return line_mapped;
  });
  return {headers, csv_body, csv_mapped, text: data};
}

const fixBooleanField = (list, field) => {
  return list.map((a) => {
    if (a[field] == "FALSE") a[field] = false;
    if (a[field] == "TRUE") a[field] = true;
    return a;
  });
}

const getFilteredEvents = (events_obj, asoc_obj, eventsFilter) => {
  const filtered_evts = {
    all_events:
        events_obj.csv_mapped.filter(eventsFilter)
  }
  
  filtered_evts.virtuals = 
    filtered_evts.all_events.filter(
        tde => {
            let asoc_rec = asoc_obj.csv_mapped.find(a => a.Detector_Name == tde.sensor_name);
            return asoc_rec.Is_Virtual;
        }
    );
  filtered_evts.physicals = 
    filtered_evts.all_events.filter(
        tde => {
            let asoc_rec = asoc_obj.csv_mapped.find(a => a.Detector_Name == tde.sensor_name);
            return !asoc_rec.Is_Virtual; // is physical
        }
    );

  return filtered_evts;
}

const strToDate = (str) => new Date(str);
const dateToStr = (date) => date.toISOString().split(".")[0];

const calculatePottentialMatches = async (relevant_events, asoc_obj) => {
  return relevant_events.virtuals.map(evt => {
    let result = {
      evt_id: evt.evt_id, event: evt,
      within3sec: [],
      targetTimes: []
    };

    const curTime = strToDate(evt.sys_time);
    for (let i = 0; i <= 3; i++) {
        const timeStrToSearch = dateToStr(curTime);
        result.targetTimes.push(timeStrToSearch);
        const parentSensor = asoc_obj.virtual_sensors
            .find(a => a.sensor_name == evt.Detector_Name)
            .Virtual_Powered_By;
        result.parentSensor = parentSensor;
        const physical_events_at = relevant_events.physicals
            .filter(ph => ph.sensor_name == parentSensor && ph.sys_time == timeStrToSearch);
        result.within3sec = [...result.within3sec, ...physical_events_at];
        curTime.add('seconds', -1); // phyiscal are BEFORE virtual
    }
    return result;
  });
}


export {
  extract_csv_data,
  fixBooleanField,
  getFilteredEvents,
  // strToDate,
  // dateToStr,
  calculatePottentialMatches,
};

