import { calculatePottentialMatches, dateToStr, extract_csv_data, fixBooleanField, getFilteredEvents, strToDate } from "./logic";

let events_txt = "";
let asoc_txt = "";

let preconditions = [];

preconditions.push(
fetch("../DataMargu/data180822.csv")
  .then((r) =>
    (r.ok) ? r :
    fetch("https://raw.githubusercontent.com/GiladanoN/MargTrainCross/master/data180822.csv")
  )
  .then((r) => r.text())
  .then((d) => (events_txt = d))
// .then(() => console.log(events_txt.length))
);

preconditions.push(
  fetch("../DataMargu/Data%20Table.csv")
  .then((r) =>
    (r.ok) ? r :
    fetch("https://raw.githubusercontent.com/GiladanoN/MargTrainCross/master/Data%20Table.csv")
  )
  .then((r) => r.text())
  .then((d) => (asoc_txt = d))
// .then(() => console.log(asoc_txt.length))
);

const main = async () => {

  console.info('Getting files ready...')
  try {
    let fetchResults = await Promise.all(preconditions);
    if (fetchResults.filter(res => !res).length > 0)
      throw 'one or more fetch has failed';
  }
  catch (err) {
    console.warn('Something wen\'t wrong getting the data. Stopping main activities.')
    return;
  }
  console.info('Files loaded. mapping out data...')

  var events_obj = extract_csv_data(events_txt);
  var asoc_obj = extract_csv_data(asoc_txt);

  asoc_obj.csv_mapped = fixBooleanField(asoc_obj.csv_mapped, "Is_Virtual");

  asoc_obj.virtual_sensors = asoc_obj.csv_mapped.filter(a => a.Is_Virtual);
  asoc_obj.physical_sensors = asoc_obj.csv_mapped.filter(a => !a.Is_Virtual);

  console.info('Data mapped out. filtering out relevant event logs...')

  let filter = 
    m => m.desc.includes("זיהוי רכבת") && !m.desc.includes("סיום");

  const train_detected_events =
    getFilteredEvents(events_obj, asoc_obj, filter);

  console.info('Relevant events were filtered. starting to calucalte pottential matches...')

  let potentials = await calculatePottentialMatches(train_detected_events, asoc_obj);

  console.info('All pottential matches were calucalte! see results below.')
  console.log(potentials);
}

var alreadyRunning;
if (!alreadyRunning)
  alreadyRunning = setTimeout(main, 0);  // run async
